import React, { useRef, useState, useEffect, useContext } from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
  useRouteMatch,
  useHistory,
  useParams,
} from "react-router-dom";

import { Typography, Collapse, Dialog } from "@material-ui/core";
import { useTransition } from "react-spring";
// context
import { GlobalContext } from "./GlobalContext";

import localForage from "localforage";
import { useSpring, animated } from "react-spring";
import axios from "axios";
import YouTube from "react-youtube";

//types
import { VideoType } from "./video-types";
import "./css/home.css";

interface HomeProps {}
const Home: React.FC<HomeProps> = () => {
  const nameStyle = useSpring({
    config: { mass: 1, friction: 10, damping: 1, frequency: 0.6 },
    from: {
      top: window.innerHeight / 2 - 40,
      left: window.innerWidth / 2 - 40,
      transform: "translate(-50%, -50%)",
      fontSize: "3rem",
    },
    top: 0,
    left: 0,
    transform: "translate(0,0)",
    fontSize: "2rem",
    delay: 1000,
  });

  const fadeIn = useSpring({
    from: {
      opacity: 0,
    },
    opacity: 1,
    delay: 1500,
  });

  const [playing, setPlaying] = useState("");

  const params = useParams<{ videoId: string }>();

  useEffect(() => {
    // console.log(params.videoId);
    if (params.videoId) {
      setPlaying(params.videoId);
    } else {
      setPlaying("");
    }
  }, [params]);

  return (
    <>
      <div className="home">
        <nav>
          <div className="left">
            <animated.p style={nameStyle} className="name">
              Jai Mangal Sharma
            </animated.p>
            {/* @ts-ignore */}
            <animated.span style={fadeIn} className="subtitle">
              FILM MAKER
            </animated.span>
          </div>
        </nav>
        <ViewVideoPage playing={playing} />
        <Works fadeIn={fadeIn} />
      </div>
    </>
  );
};

function ViewVideoPage({ playing }: { playing: string }) {
  const [video, setVideo] = useState<VideoType>();

  const { videos } = useContext(GlobalContext);

  const ref = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (playing && videos && videos.length) {
      // ref.current?.scrollIntoView({ behavior: "smooth" });
      setVideo(
        videos.filter((vid) => vid.snippet.resourceId.videoId === playing)[0]
      );
      window.scrollTo({ top: 0, behavior: "smooth" });
    }
  }, [playing, videos]);

  return (
    <Collapse in={!!playing}>
      <div ref={ref} className="player">
        <YouTube containerClassName="bigplayer" videoId={playing} />
        <div className="video-details">
          <p className="title">{video?.snippet.title}</p>
          <div className="subtitle">
            {video?.snippet.description.split("\n").map((line, i) => {
              return <p key={i}>{line}</p>;
            })}
          </div>
        </div>
      </div>
    </Collapse>
  );
}

function Works({ fadeIn }: { fadeIn: any }) {
  return (
    <animated.div style={fadeIn} className="works">
      <p className="work-title">My Works</p>
      <WorkVideos />
    </animated.div>
  );
}
function WorkVideos() {
  const { videos } = useContext(GlobalContext);

  /* return (
    <>
      {transitions.map(function ({ item, props, key }: any) {
        // <Video key={key} video={item} animstyle={props} />
        console.log(item, props, key);
        return <></>;
      })}
    </>
  ); */
  return (
    <div className="work-videos">
      {videos?.map((video, i) => (
        <Video key={i} index={i} video={video} />
      ))}
    </div>
  );
}

function getThumbnail(
  video: VideoType
): { mq: string; maxhd: string; sd: string } {
  return {
    mq: `http://img.youtube.com/vi/${video.snippet.resourceId.videoId}/mqdefault.jpg`,
    maxhd: `http://img.youtube.com/vi/${video.snippet.resourceId.videoId}/maxresdefault.jpg`,
    sd: `http://img.youtube.com/vi/${video.snippet.resourceId.videoId}/sddefault.jpg`,
  };
}
function Video({
  video,
  index,
}: {
  video: VideoType;
  animstyle?: any;
  index: number;
}) {
  const [state, setState] = useState("idel");
  const [showdetails, setShowdetails] = useState(false);
  const [style, set] = useSpring(() => {
    return { opacity: 0, y: 100 };
  });

  useEffect(() => {
    set({ opacity: 1, y: 0, delay: 1500 + index * 100 });
  }, [set, index]);

  const history = useHistory();

  return (
    <>
      <animated.div
        // @ts-ignore
        style={{ ...style, position: index === 0 ? "relative" : "" }}
        className="work-video"
        onClick={() => {
          history.push("/works/" + video.snippet.resourceId.videoId);
        }}
        onMouseEnter={() => setShowdetails(true)}
        onMouseLeave={() => setShowdetails(false)}
      >
        {video && state ? (
          <img src={getThumbnail(video).mq} alt="" className="thumbnail" />
        ) : (
          <></>
        )}
        <Collapse in={showdetails}>
          <div className="details">
            <p className="title">{video.snippet.title}</p>
          </div>
        </Collapse>
      </animated.div>
    </>
  );
}

export default Home;
